import { BasicColumn, FormSchema, useRender } from '@/components/Table';
import { defHttp } from '@/utils/http/axios';
import { useUserStore } from '/@/store/modules/user';

enum Api {
  getSafetyToolsCheckPage = '/po/safety-tools-check/page',
  // deleteSafetyToolsCheckData = '/po/safety-tools-check/delete',
  getSafetyToolsCheckData = '/po/safety-tools-check/get',
  exportSafetyToolsCheckData = '/po/safety-tools-check/export-excel',
}

// 查询
export function getSafetyToolsCheckPage(params) {
  return defHttp.get({ url: Api.getSafetyToolsCheckPage, params });
}
export function getSafetyToolsCheckData(params) {
  return defHttp.get({ url: Api.getSafetyToolsCheckData, params });
}
// 删除
// export function deleteSafetyToolsCheck(id: string) {
//   return defHttp.delete({ url: Api.deleteSafetyToolsCheckData + '?id=' + id });
// }
export function exportSafetyToolsCheckData(params) {
  return defHttp.download(
    { url: Api.exportSafetyToolsCheckData, params },
    { isReturnNativeResponse: true },
  );
}
const statusColor = {
  0: '#dc0000', // 草稿
  1: '#338ed1', // 处理中
  2: '#30ca5a', // 通过
  3: '#929292', // 不通过
  4: '#929292', // 已取消
  5: '#929292', // 已终止
  6: '#fda941', // 退回/驳回
};
